import * as React from "react"
import { Link } from "gatsby"
import { StaticQuery, graphql } from "gatsby"
// import Layout from "../../Layout/Layout"
import { CardGroup, Card, ButtonGroup, Button } from "react-bootstrap"

let test2 = {}
var element = []

function transformer(data) {
	const test = JSON.stringify(data, null, 4);
	test2 = JSON.parse(test);
	const articles = test2.allMongodbSafeparasportDbArticles.nodes;
	console.log(articles)

	// console.log("test1:");
	// console.log(data);

	// console.log(test2.allMongodbSafeparasportDbArticles.nodes[0].title);

	// for (let index = 0; index < test2.allMongodbSafeparasportDbArticles.nodes.length; index++) {
	// 	element.push(test2.allMongodbSafeparasportDbArticles.nodes[index].title);
	// }
	let group = articles.map(page => (
		<Card style={{ width: '100%', height: 'fit-content', boxShadow: '4px 2px 2px' }}>
			<Card.Title>{page.title}</Card.Title>
			<br></br>
			<ButtonGroup>
				<Link to={`view/${page.slug}`} style={{ width: 'fit-content' }}><Button variant="primary">Read Article</Button></Link>
				<Link to={`view/${page.slug}`} style={{ width: 'fit-content' }}><Button variant="primary">Edit Article</Button></Link>
			</ButtonGroup>
		</Card>
	)
	)

	return group;
}

const ArticlesIndex = () => (
	<>
		<StaticQuery
			query={graphql`
			{
			allMongodbSafeparasportDbArticles {
				nodes {
				title
				slug
				}
			}
			}
		`}
			render={data => (<CardGroup style={{ width: '100%' }}>{transformer(data)}</CardGroup>)}

		></StaticQuery>
		<p>hej</p>
	</>

)

export default ArticlesIndex

